import { Logger } from '@uniformdev/common';

import { PublishProvider, serverLogger, PublishProviderOptions } from '.';

export class FakePublishProvider implements PublishProvider {
    private readonly logger: Logger;

    constructor({ logger = serverLogger }: PublishProviderOptions) {
        logger.info('Initializing FakePublishProvider');

        this.logger = logger;
    }

    get behavior(): 'replace-on-deploy' | 'update-on-deploy' {
        return 'update-on-deploy';
    }

    async deploy(path: string): Promise<void> {
        this.logger.info('Deploying ' + path + ' (FakePublishProvider)');
    }
}
